@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Robotolight.woff2") format("woff2"), url("../fonts/Robotolight.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-size: 16px;
  line-height: 32.4px;
  font-family: 'Comfortaa', cursive;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.case {
  width: 100%;
  max-width: 1680px;
  margin: 0 auto;
  padding-left: 5vw;
  padding-right: 5vw; }

.ind-block {
  padding-top: 5.5rem;
  padding-bottom: 5.5rem;
  position: relative;
  clear: both;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(50vw - 50%); }

#teasers-section {
  background-color: #e6f4ff;
  position: relative;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(50vw - 50%);
  padding: 0; }

.grid {
  display: grid; }

.listgrid-item.color-no {
  background-color: rgba(255, 255, 255, 0);
  color: #1a3c55; }

.listgrid-list {
  grid: none/repeat(4, 1fr);
  gap: 1.25rem;
  padding-top: 1rem;
  width: 100%;
  position: relative; }

.listgrid-list.grid.grids-1 {
  grid: none/repeat(1, 1fr);
  gap: 1.25rem; }

.listgrid-list.grid.grids-2 {
  grid: none/repeat(2, 1fr);
  gap: 1.25rem; }

.listgrid-list.grid.grids-3 {
  grid: none/repeat(3, 1fr);
  gap: 1.25rem; }

.listgrid-list.grid.grids-4 {
  grid: none/repeat(4, 1fr);
  gap: 1.25rem; }

.listgrid-list.grid.grids-5 {
  grid: none/repeat(5, 1fr);
  gap: 1.25rem; }

.listgrid-list.grid.grids-6 {
  grid: none/repeat(6, 1fr);
  gap: 1.25rem; }

.listgrid-item.listgrid-vertical {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center; }

.listgrid-item.listgrid-horizontal {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%; }

.listgrid-list.listgrid-horizontal.grids-3 {
  grid-template-columns: repeat(auto-fit, minmax(393px, 1fr)); }

.listgrid-list.listgrid-horizontal.grids-4 {
  grid-template-columns: repeat(auto-fit, minmax(288px, 1fr)); }

.listgrid-list.listgrid-horizontal.grids-5 {
  grid-template-columns: repeat(auto-fit, minmax(272px, 1fr)); }

.listgrid-list.listgrid-vertical.grids-4 {
  grid-template-columns: repeat(auto-fit, minmax(273px, 1fr)); }

.listgrid-list.listgrid-vertical.grids-5 {
  grid-template-columns: repeat(auto-fit, minmax(245px, 1fr)); }

.listgrid-item {
  position: relative;
  overflow: hidden;
  border-radius: 36px;
  background-color: #e5eef8;
  color: #1a3c55;
  width: 100%;
  overflow: hidden; }

.listgrid-item.listgrid-horizontal .listgrid-image {
  margin-bottom: 1rem;
  margin-left: auto; }

.listgrid-item.listgrid-horizontal .listgrid-image {
  margin-right: 0.5rem;
  margin-left: 0;
  padding: 0.5rem 0.3rem 0.3rem 0.3rem;
  max-width: 40%;
  flex-shrink: 0; }

.listgrid-icon {
  width: 10.5rem;
  height: 10.5rem;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  transition: .3s;
  padding: 1rem 1rem 1rem 1rem;
  max-width: 100%;
  align-items: center;
  justify-content: center;
  background-color: #00569c;
  color: #f7f7f7;
  clip-path: ellipse(45% 45% at 50% 50%); }
  .listgrid-icon.height-8 {
    height: 8rem;
    width: 8rem; }

.listgrid-item.listgrid-vertical .listgrid-icon {
  margin-top: 1rem;
  margin-bottom: 0.3rem; }

.listgrid-image:hover {
  transform: scale(1.1);
  transition: 0.3s; }

.listgrid-image img {
  max-width: 100%;
  height: auto; }

.listgrid-icon img {
  max-width: 80%;
  height: auto; }

.color-no .listgrid-icon img {
  max-width: 100%;
  height: 100%; }

.listgrid-info {
  padding: 0.8rem 1rem 2rem 0.8rem; }

.listgrid-item.listgrid-horizontal .listgrid-info {
  padding-left: 1rem;
  height: 100%;
  align-self: flex-start; }

.listgrid-title {
  font-size: 23.4px;
  font-weight: 480;
  line-height: 36px; }

.listgrid-text {
  margin-top: 1rem; }

@media screen and (max-width: 992px) {
  .listgrid {
    padding-top: 6.5rem;
    padding-bottom: 6.5rem; }
  .listgrid-list {
    display: flex;
    flex-wrap: wrap;
    grid: none;
    gap: 1.25rem 0.625rem;
    justify-content: center; } }

